<template>
    <div class="operation">
        <ul class="operations">
            <li v-for="(item,index) in operations">
                <span>{{item.recent}}</span><span><strong>{{item.controller}}</strong></span><span>{{item.operate}}</span><span>{{item.target}}</span>
            </li>
        </ul>
    </div>
</template>
<script>
import {mapState} from 'vuex'
export default {
    data(){
        return {
            flag: false,
            operate: []
        }
    },
    computed: {
        ...mapState({
            dynamicData: 'dynamicData'
        }),
        dangerDynamicData(){
            return this.dynamicData.filter(item => {
                return item.danger == true;
            })
        },
        operations: {
            get(){
                return this.flag ? this.operate : this.dangerDynamicData;
            },
            set(v){
                this.operate = v;
            }
        }
    },
    methods: {
        toggleData(val){
            this.flag = true;
            if(val == 0){
                this.operations = this.dangerDynamicData;
            }else{
                this.operations = this.dynamicData;
            }
            console.log('this.operations', this.operations);
        }
    },
    beforeCreate() {
        console.log('Operation.vue beforeCreate');
    },
    created() {
        console.log('Operation.vue Created');
    },
    beforeMount() {
        console.log('Operation.vue beforeMount');
    },
    mounted() {
        console.log('Operation.vue mounted');
        console.log(this.$route);
    },
    beforeUpdate() {
        console.log('Operation.vue beforeUpdate');
    },
    updated() {
        console.log('Operation.vue updated');
    },
    beforeDestroy() {
        console.log('Operation.vue beforeDestroy');
    },
    destroyed() {
        console.log('Operation.vue destroyed');
    },
}
</script>
